@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@600&display=swap);
*{
    margin:0;
    padding: 0;
    font-family: 'Josefin Sans', sans-serif;
    box-sizing: border-box;
}
body{
    background-color: rgba(233, 233, 233, 0.884);
    width: 100%;
    height: 100vh;
    overflow: hidden;
}
.container{
    width: 100%;
    height:100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;  
}
.main_div{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width:300px;
    height: 50vh;
    margin:auto;
    border-radius: 10px;
    background-color: rgb(175, 193, 245);
}
h1{
    margin-top:10px;
    margin-bottom:20px;
    width:100%;
    padding: 10px;
    text-align: center;
    background-color: rgb(232, 246, 248);
}
input{
    width:70%;
    padding:5px;
    margin-top: 4px;
    margin-bottom: 20px;
    border:none;
    outline: none;
    border-radius: 10px;
}

 @media (min-width: 576px) { 
    .main_div{
        width:350px;
    }
 }

@media (min-width: 768px) { 
    .main_div{
        width:350px;
    }
 }

@media (min-width: 992px) { 
    .main_div{
        width:350px;
    }
 }

@media (min-width: 1200px) { 
    .main_div{
        width:350px;
    }
 }
@media (min-width: 1400px) { 
    .main_div{
        width:450px;
    }
 }
.navbar{
    padding:10px;
    background-color: rgb(13, 4, 95);
}
a{
    color: white;
    text-decoration: none;
    margin: 5px;
}
.change_color{
    color:rgb(208, 230, 255);
}
.iTemp_css{
    background-color:rgb(255, 0, 0)
}
.iTemp_css p{
    text-align: justify;
    text-transform: capitalize;
    line-height: 20px;
    padding: 10px;
    color:white;
}
footer{
    padding: 5px;
    text-align: center;
    background-color: white;
    position:absolute;
    bottom:0;
    width:100%;
}
